<template>
    <div class="closed-auction">
        <h1 class="title">{{$localizeFilter('closed_auction')}}</h1>
        <div class="coins-tabs-block" v-if="coins">
            <div class="coin-tab" :class="(coin.selected ? 'selected' : '')" v-for="(coin,idx) in coins" v-bind:key="idx" @click="selectCoin(idx)">
                <img class="coin-tab-coin-icon" :src="require(`@/assets/newImages/coins/icons/${$filters.lowercase(coin.coinTicker)}_icon.png`)">
                <p class="coin-tab-coin-ticker">{{coin.coinTicker}}</p>
            </div>
        </div>

        <div class="window light" v-if="selectedCoin">
            <div class="highlight-block" v-if="showHighlight"></div>
            <p class="window-text" v-if="auctionRunning">{{$localizeFilter('closed_auction_running_text')}}
                <span class="coin-link" @click="openCoinPage()">{{currentCoinName}}</span>{{':'}}
            </p>
            <p class="window-text" v-else-if="auctionFinished">{{$localizeFilter('closed_auction_finished_text_1')}}
                <span class="coin-link" @click="openCoinPage()">{{currentCoinName}}</span>{{ ' ' + $localizeFilter('closed_auction_finished_text_2')}}
            </p>
            <p class="window-text" v-else>{{$localizeFilter('closed_auction_waiting_text_1')}}
                <span class="coin-link" @click="openCoinPage()">{{currentCoinName}}</span>
                {{$localizeFilter('closed_auction_waiting_text_2')}}
            </p>
            <div class="coin-timer-block">
                <p class="timer" v-if="!auctionFinished" :class="auctionRunning ? 'auction-running' : ''">{{ timeLeft }}</p>
                <img :src="require('@/assets/newImages/coins/'+currentCoinName+'.png')" class="image">
            </div>
            <ul class="content-table" v-if="auctionRunning">
                <li class="content-line">
                    <div class="content-cell-1">
                        <p class="bold-text">{{startPriceText}}</p>
                    </div>
                    <div class="content-cell-2">
                        <p class="bold-text blue-text">{{'$'+startPrice}}</p>
                    </div>
                </li>
                <li class="content-line">
                    <div class="content-cell-1">
                        <p class="bold-text">{{$localizeFilter('course_after_completion_closed_auctions')}}</p>
                    </div>
                    <div class="content-cell-2">
                        <p class="bold-text">{{'$'+selectedCoin?.rateAfterClosedPresale}}</p>
                    </div>
                </li>
                <li class="content-line">
                     <div class="content-cell-1 limit-cell-1">
                        <p class="bold-text limit-text">{{$localizeFilter('you_can_buy')}}</p>
                        <div class="empty-space"></div>
                    </div>
                    <div class="content-cell-2 limit-cell-2">
                        <p class="bold-text limit-text">{{youCanBuy + ' '}}<span class="blue-text">{{selectedCoin?.coinTicker}}</span></p>
                        <button class="increase-limit-button" @click="increaseLimit()">{{$localizeFilter('increase_limit')}}</button>
                    </div>
                </li>
            </ul>
            <ul class="content-table" v-else-if="auctionFinished">
                <li class="content-line">
                    <div class="content-cell-1">
                        <p class="bold-text">{{$localizeFilter('coin_rate')}}</p>
                    </div>
                    <div class="content-cell-2">
                        <p class="bold-text">{{'$'+selectedCoin?.rateAfterClosedPresale}}</p>
                    </div>
                </li>
            </ul>
            <p class="bold-text" v-else>{{startPriceText + ' '}}<span class="blue-text">{{'$'+(startPrice ?? '')}}</span></p>
            <!-- <button class="increase-limit-button" v-if="!auctionRunning" :style="'height: 44px!important;'" @click="increaseLimit()">{{$localizeFilter('increase_limit')}}</button> -->
            <button class="basic-button" v-if="auctionRunning || auctionFinished" @click="buyCoin(selectedCoin?.coinTicker, youCanBuy)">{{$localizeFilter('buy_now')}}</button>
            <!-- <img class="telegram-image" src="@/assets/newImages/telegram.png"></img> -->
            
            <!-- <button class="increase-limit-button" v-if="!auctionRunning" :style="'height: 44px!important;'" @click="increaseLimit()">{{$localizeFilter('increase_limit')}}</button> -->
            <!-- <button class="basic-button" v-if="auctionRunning || auctionFinished" @click="openBot">{{$localizeFilter('buy_now')}}<img class="telegram-image" src="@/assets/newImages/telegram.png"></button> -->
      </div>
      <img class="whale-circle" src="@/assets/newImages/whale_circle.png">

      <IncreaseLimitModal v-if="showIncreaseLimitModal" :coin="selectedCoin" :rate="startPrice" :clientId="clientId" @closeModal="closeModals" />
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import IncreaseLimitModal from '../components/newModals/IncreaseLimitModal.vue'

export default defineComponent({
    data: () => ({
        selectedCoin: null,
        currTimestamp: +Date.now(),
        timerEndTimestamp: +Date.now(),
        showHighlight: false,
        coins: null,
        showIncreaseLimitModal: false
    }),
    setup() {
    },
    computed: {
        clientId() {
            return this.$store.getters.profile.id
        },
        auctionRunning() {
            return this.selectedCoin?.type === "live";
        },
        auctionFinished() {
            return this.selectedCoin?.type === "finished";
        },
        currentCoinName() {
            return this.selectedCoin?.coin;
        },
        youCanBuy(){
            return this.selectedCoin ? Math.min((this.selectedCoin.youCanBuy || 0) + this.selectedCoin.limit, (this.selectedCoin.maxUSDBuyAmount || Infinity) / this.startPrice) : 0;
        },

        timeLeft() {
            let totalTime = Math.floor((this.timerEndTimestamp - this.currTimestamp) / 1000);
            if (totalTime <= 0) {
                if (this.selectedCoin?.type == "announce") {
                    this.startAuction();
                }
                return "00:00";
            }
            let hours = Math.floor(totalTime / 3600);
            let otherTime = totalTime % 3600;
            let minutes = Math.floor(otherTime / 60);
            let seconds = Math.floor(otherTime % 60);
            return `${hours}:${minutes >= 10 ? minutes : ("0" + minutes)}:${seconds >= 10 ? seconds : ("0" + seconds)}`;
        },
        startPriceText() {
            if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolder") {
                return localizeFilter("starting_price_for_DOM_coin_holders");
            }
            else if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolderReferral") {
                return localizeFilter("starting_price_for_DOM_coin_holders_referral");
            }
            else {
                return localizeFilter("starting_price");
            }
        },
        startPrice() {
            if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolder") {
                return this.selectedCoin?.startPriceDOMHolder;
            }
            else if (this.selectedCoin?.yourStartPrice == "startPriceDOMHolderReferral") {
                return this.selectedCoin?.startPriceDOMHolderReferral;
            }
            else {
                return this.selectedCoin?.startPriceOther;
            }
        }
    },
    methods: {
        increaseLimit() {
            this.showIncreaseLimitModal = true
        },
        closeModals(needUpdateInfo) {
            this.showIncreaseLimitModal = false
            if(needUpdateInfo) {
                this.loadData();
            }
        },
        selectCoin(idx) {
            for (let i = 0; i < this.coins.length; i++) {
                if (i === idx) {
                    this.coins[i].selected = true;
                    this.selectedCoin = this.coins[i];
                    this.setupTimers();
                }
                else {
                    this.coins[i].selected = false;
                }
            }
        },
        setupTimers() {
            clearInterval(this.timerInterval);
            this.timerEndTimestamp = this.auctionRunning || this.auctionFinished ? this.selectedCoin?.presaleEndTimestamp : this.selectedCoin?.presaleStartTimestamp;
            this.timerInterval = setInterval(() => {
                this.currTimestamp = Date.now();
            }, 1000);
        },
        startAuction() {
            if (!this.auctionRunning) {
                this.auctionRunning = true;
                clearInterval(this.timerInterval);
                this.loadData();
            }
        },
        async buyCoin(coinTicker, maxAmount) {
            if (!coinTicker) {return}
            await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
            await this.$store.dispatch('setPaymentCoinMaxAmount', {maxAmount: maxAmount})
            this.$nextTick(()=>{
                this.$store.dispatch('setShowPaymentCoinModule', {value: true})
            })
        },
        openCoinPage() {
            //window.open('#/coins/' + this.currentCoinName)
        },
        async loadData() {
            try {
                const serverData = (await this.$store.dispatch("getClosedPresaleParameters")).coins;
                this.coins = serverData;
                if (serverData.length > 0) {
                    this.selectCoin(0);
                }
                // console.log(this.coins, this.selectedCoin)
            }
            catch (e) {
                this.$error(e);
            }
        }
    },
    created: function () {
        this.timerInterval = null;
    },
    async mounted() {
        await this.loadData();
        const exchangeClosedAuctionPageOpened = JSON.parse(localStorage.exchangeClosedAuctionPageOpened ?? null);
        this.showHighlight = true; //(exchangeClosedAuctionPageOpened != true)
        if (exchangeClosedAuctionPageOpened != true) {
            localStorage.exchangeClosedAuctionPageOpened = true;
            window.dispatchEvent(new CustomEvent("exchangeClosedAuctionPageOpened", {
                detail: {
                    value: localStorage.getItem("exchangeClosedAuctionPageOpened")
                }
            }));
        }
        setTimeout(() => {
            this.showHighlight = false;
        }, 2000);
    },
    unmounted() {
        clearInterval(this.timerInterval);
    },
    components: { IncreaseLimitModal }
})
</script>

