<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal increase-limit-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            </div>
        <div class="modal-content">
            <div class="top-block">
                <p class="window-title">{{$localizeFilter('increase_limit_modal_title', [coin.coinTicker])}}</p>
            </div>
            
            <div class="text-amount-block">
                <div class="amount-block">
                    <p class="window-text">{{$localizeFilter('increase_limit_modal_text_1', [coin.coin])}}</p>
                    <div class="input-field">
                        <input id="amount" class="custom-input-2" :placeholder="$localizeFilter('amount_of', [coin.coinTicker])" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
                        <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                            {{ error.$message }}
                        </small>
                    </div>
                </div>
            </div>

            <div class="burn-cwpt-block">
                <p class="window-text burn-cwpt-text">{{$localizeFilter('increase_limit_modal_text_2')}}:</p>
                <p class="burn-cwpt-amount">{{`${amountCWPT} CWPT`}}</p>
            </div>

            <div class="button-block">
                <button class="continue-button" @click="submitHandler">{{$localizeFilter('continue')}}</button>
            </div>
        </div>
    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'
import wallet from '../../store/wallet';

export default defineComponent({
   props: {
        coin: {
            type: Object,
            required: true
        },
        rate: {
            type: Number,
            required: true
        },
        clientId: {
            type: Number,
            required: true
        }
    },
    validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
            },
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: ()=>({
        amount: null,
        minAmount: 0,
    }),
    computed: {
        amountCWPT() {
            return +((this.amount || 0)*this.rate / 10).toFixed(6);
        }
    },
    methods: {
        closeModal(reload) {
            this.$emit('closeModal', reload)
        },
        async burnCWPT(){
            let limitIncreased = false;
            if(window.tronWeb) {
                let account = await tronWeb.trx.getAccount();

                let burnAmount = this.amountCWPT;
                // let burnAmount = 100;

                burnAmount = burnAmount * 1000000 + '000000000000';

                // tronLink.request({method: 'tron_requestAccounts'})

                let clientId = this.clientId;

                let contract = await tronWeb.contract().at('TLwGQimcVEuAdcfMRcjVxDCVy4h8wgEnBz'); 
                // let contract = await tronWeb.contract().at('TQyqdZkBRZddFBGG8a7rAsEYv4TYV4p9vg');

                let coinId = this.coin.coinId;

                let walletAddress = tronWeb.defaultAddress.base58;

                try {
                    let transactionId = await contract.burn(burnAmount).send({
                        feeLimit:100_000_000,
                        callValue:0,
                        // shouldPollResponse:true
                    });

                    console.log(transactionId);


                    let message = `CapitalWhale User Id: ${clientId}; Transaction Id: ${transactionId}; Wallet Address: ${walletAddress}; Coin Id: ${coinId}`;

                    console.log(message);

                    // let signature = (await tronWeb.trx.sign(tronWeb.fromUtf8(message)));

                    var hexStrWithout0x = tronWeb.toHex(message).replace(/^0x/, '');
                    // conert hex string to byte array
                    var byteArray = tronWeb.utils.code.hexStr2byteArray(hexStrWithout0x)
                    // keccak256 computing, then remove "0x" 
                    var strHash= tronWeb.sha3(byteArray).replace(/^0x/, '');
                    // sign 
                    var signedStr = await tronWeb.trx.sign(strHash);
                    var tail = signedStr.substring(128, 130);
                    if(tail == '01')
                    {
                        signedStr = signedStr.substring(0,128)+'1c';
                    }
                    else if(tail == '00')
                    {
                        signedStr = signedStr.substring(0,128)+'1b';
                    }

                    var res = await tronWeb.trx.verifyMessage(strHash,signedStr,walletAddress)
                    console.log(res);

                    try {
                        console.log('here')
                        const formData = {address: walletAddress, signature: signedStr, transactionId, coinId};
                        await this.$store.dispatch('burnCWPT', formData)
                        limitIncreased = true;
                        // this.$message(this.$localizeFilter('link_dom_success'))
                        // this.$emit('domBinded')
                    } catch(e) {
                        this.$error(e)
                    } 


                    // var res = await tronWeb.trx.verifyMessage(tronWeb.fromUtf8(message),signature,walletAddress)

                    // console.log('VERIFY:', res);
                    


                } catch (error) {
                    console.log(error);
                }

            } else {
                this.$error('TRON wallet not connected')
            }

            return limitIncreased;
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error) {
                return;
            }
            // const data = {clientId: this.clientId, coinTicker: this.coin.coinTicker, rate: this.rate, amount: this.amount, amountCWPT: this.amountCWPT}
            // try {
            //     await this.$store.dispatch('increaseBuyCoinLimit', data)
            //     this.$message(localizeFilter('limit_increased_successful'))
            //     this.$emit('closeModal')
            // } catch(e) {
            //     this.$error(e)
            // }
            let needUpdateInfo = await this.burnCWPT();
            this.closeModal(needUpdateInfo);


        //    this.$emit('choosePaymentMethod', {amount: Number(this.amount), walletAddress: this.walletAddress})
        }
  }
})
</script>


